
#app {
  height:100%;
}
.css-top{
  margin: 0 10px;
}

body,
textarea {
  margin: 0;
  padding: 0;
  font-family: "PingFang SC", "Microsoft YaHei", SimHei, Arial, SimSun;
  font-size: 1rem;
}
html,
body {
  height: 100%;
  width: 100%;
}
body {
  background-color: #ccc;
}
.ds-n {
  display: none;
}
.vue-markdown-container {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 10px;
}
.vue-markdown-container .css-markdown {
    overflow-x: hidden;
    overflow-y: scroll;
    word-break: break-all;
    word-wrap: break-word;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    border: 0;
    height: 96%;
    padding: 2%;
}
.vue-markdown-container .css-markdown.typein-container {
      background-color: #272822;
      color: #fff;
      resize: none;
      outline: none;
}
.vue-markdown-container .css-markdown.view-container {
      background-color: #f7f7f7;
}
